.c-pricing-section__title { font-weight: 300; font-size: 3.6rem; line-height: 1.2; }

.c-pricing__title { font-size: 1.8rem; font-weight: 600; line-height: 1.4; letter-spacing: 0.01em; }

.c-pricing__feature { font-size: 1.2rem; line-height: 1.5; }

* { -webkit-box-sizing: border-box; -moz-box-sizing: border-box; box-sizing: border-box; }

*:before, *:after { -webkit-box-sizing: border-box; -moz-box-sizing: border-box; box-sizing: border-box; }

html { font-size: 10px; -ms-text-size-adjust: 100%; -webkit-text-size-adjust: 100%; }

body { margin: 0; font-family: "Open Sans", sans-serif; font-size: 1.4rem; line-height: 1.4; font-weight: 400; -moz-osx-font-smoothing: grayscale; -webkit-font-smoothing: antialiased; }

h1, h2, h3, h4, p, blockquote, figure, ol, ul { margin: 0; padding: 0; }

main, li { display: block; }

h1, h2, h3, h4 { font-size: inherit; }

strong { font-weight: bold; }

a, button { color: inherit; transition: .3s; }

a { text-decoration: none; }

button { overflow: visible; border: 0; font: inherit; -webkit-font-smoothing: inherit; letter-spacing: inherit; background: none; cursor: pointer; }

::-moz-focus-inner { padding: 0; border: 0; }

:focus { outline: 0; }

img { max-width: 100%; height: auto; border: 0; }

p, ul, ol, pre, table, blockquote { margin-top: 0; margin-bottom: 2rem; }

b, strong, em, small, code { line-height: 1; }

ul ul, ul ol, ol ol, ol ul { margin-top: 0; margin-bottom: 0; }

.c-pricing__item:before { content: " "; position: absolute; display: block; }

.container { max-width: 1170px; margin-right: auto; margin-left: auto; padding-left: 1.5rem; padding-right: 1.5rem; }

.container:before, .container:after { content: " "; display: table; }

.container:after { clear: both; }

.row { margin-left: -1.5rem; margin-right: -1.5rem; }

.row:before, .row:after { content: " "; display: table; }

.row:after { clear: both; }

[class^=col-] { position: relative; float: left; min-height: 1px; padding-left: 1.5rem; padding-right: 1.5rem; }

.col-xs-1 { width: 8.333%; }

.col-xs-2 { width: 16.66666666666667%; }

.col-xs-3 { width: 25%; }

.col-xs-4 { width: 33.333%; }

.col-xs-5 { width: 41.66666666666667%; }

.col-xs-6 { width: 50%; }

