.col-xs-8 { width: 66.66666666666667%; }

.col-xs-9 { width: 75%; }

.col-xs-10 { width: 83.33333333333333%; }

.col-xs-11 { width: 91.66666666666667%; }

.col-xs-12 { width: 100%; }

