.c-pricing--flx, .c-pricing__item--flx { display: flex; }

.c-pricing--flx { max-width: 1170px; margin-left: auto; margin-right: auto; flex-wrap: wrap; flex-direction: row; justify-content: center; }

.c-pricing__item--flx { flex-direction: column; flex: 0 1 50rem; align-items: stretch; }

@media (min-width: 768px) { .c-pricing__item--flx { flex: 1; } }

.c-pricing-section { padding: 7rem 0; background-color: #323a40; }

.c-pricing-section__title { position: relative; margin-bottom: 8rem; padding: 1rem 0; color: #fff; line-height: 1.2; text-align: center; }

.c-pricing-section__title:after { content: ' '; position: absolute; bottom: -1rem; left: 50%; display: block; width: 18rem; height: 0.2rem; margin-left: -9rem; background-color: #384047; }

.c-pricing-section--flx .c-pricing-section__title { padding-left: 1.5rem; padding-right: 1.5rem; }

.c-pricing__item { position: relative; max-width: 50rem; margin: 0 auto 3rem; padding: 4rem; background: #384047; }

.c-pricing__item:before { top: 0; left: 0; width: 100%; height: 0.6rem; opacity: 0; background: #3079ab; transition: opacity 0.5s; }

.c-pricing__item:hover:before { opacity: 1; }

.c-pricing__title { margin-bottom: 1rem; border-bottom: 0.2rem solid #68717A; padding: 1rem 0; color: #fff; }

.c-pricing__price { margin-bottom: 2rem; color: #3079ab; font-size: 3.6rem; font-weight: 600; }

.c-pricing__currency { padding-right: 0.2rem; font-size: 0.5em; vertical-align: super; }

.c-pricing__text { border-bottom: 0.2rem solid #68717A; padding-bottom: 1rem; color: #68717A; font-weight: 600; }

.c-pricing__features { min-height: 14rem; }

.c-pricing__feature { color: #68717A; font-style: italic; }

.c-pricing__btn { display: inline-block; width: 100%; margin-right: -0.4rem; padding: 1.5rem 2.5rem; color: #fff; font-weight: 700; text-align: center; background-color: #3079ab; border-radius: 2.5rem; transition: background-color 0.3s; }

.c-pricing__btn:hover { background-color: #255d83; }

.c-pricing--red .c-pricing__item:before { background-color: #e15258; }

.c-pricing--red .c-pricing__price { color: #e15258; }

.c-pricing--red .c-pricing__btn { background-color: #e15258; }

.c-pricing--red .c-pricing__btn:hover { background-color: #d9272e; }

.c-pricing__item--flx { margin: 0 1.5rem 3rem; }

@media (min-width: 768px) { .c-pricing__item { padding: 4rem 2rem; }
  .c-pricing__item--flx .c-pricing__btn { margin-top: auto; } }

@media (min-width: 992px) { .c-pricing__item { padding: 4rem; } }
