* {
	@include box-sizing(border-box);
}
*:before,
*:after {
	@include box-sizing(border-box);
}


html {
	font-size: 10px;
	-ms-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
}
body {
	margin: 0;

	font-family: $open-sans;
	font-size: $base-font-size;
	line-height: $base-line-height;
	font-weight: 400;

	-moz-osx-font-smoothing: grayscale;
	-webkit-font-smoothing: antialiased;
}
h1,
h2,
h3,
h4,
p,
blockquote,
figure,
ol,
ul {
	margin: 0;
	padding: 0;
}
main,
li {
	display: block;
}
h1,
h2,
h3,
h4 {
	font-size: inherit;
}
strong {
	font-weight: bold;
}
a,
button {
	color: inherit;
	transition: .3s;
}
a {
	text-decoration: none;
}
button {
	overflow: visible;
	border: 0;
	font: inherit;
	-webkit-font-smoothing: inherit;
	letter-spacing: inherit;
	background: none;
	cursor: pointer;
}
::-moz-focus-inner {
	padding: 0;
	border: 0;
}
:focus {
	outline: 0;
}
img {
	max-width: 100%;
	height: auto;
	border: 0;
}

p, ul, ol, pre, table, blockquote {
	margin-top: 0;
	margin-bottom: rem(20);
}

b, strong, em, small, code {
	line-height: 1;
}

ul ul, ul ol, ol ol, ol ul {
	margin-top: 0;
	margin-bottom: 0;
}




